<template>
  <div class="list-row">
    <div class="list-row d-flex justify-content-center">
      <h1 class="my-4">Lista de Productos</h1>
    </div>



    <div class="col-md-14">
      <div class="input-group my-4">
        <input
          type="text"
          class="form-control p-3"
          placeholder="Buscar por nombre"
          v-model="search"
        />
      </div>
    </div>



    <div v-if="search" class="col-md-12">
      <a
        class="
          list-group-item list-group-item-action list-group-item-light
          d-flex
          justify-content-between
        "
        :class="{ active: index == currentIndex }"
        v-for="(item, index) in filtroBusqueda"
        :key="index"
        @click="push(item.id)"
      >
        <span class="my-4">{{ item.nombre }}</span>
        <span class="my-4">${{ item.precio }}</span>
      </a>
    </div>

    <div v-else class="col-md-12">
      <a
        class="
          list-group-item list-group-item-action list-group-item-light
          d-flex
          justify-content-between
        "
        :class="{ active: index == currentIndex }"
        v-for="(item, index) in items"
        :key="index"
        @click="push(item.id)"
      >
        <span class="my-4">{{ item.nombre }}</span>
        <span class="my-4">${{ item.precio }}</span>
      </a>
    </div>
  </div>
</template>

<script>
import { useLoadItems, deleteItem } from "@/firebase";

export default {
  setup() {
    const items = useLoadItems();
    return { items, deleteItem };
  },
  data() {
    return {
      search: "",
      currentIndex: -1,
    };
  },
  computed: {
    filtroBusqueda: function () {
      return this.items.filter((item) => {
        return item.nombre.toLowerCase().match(this.search.toLowerCase());
      });
    },
  },
  methods: {
    push(id) {
      this.$router.push("/item/" + id);
    },
  },
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>